import React, { Component } from 'react'
import './styles.css';
export default class ViewSelector extends Component {
    constructor (props) {
        super(props);
        this.viewSelectorItemClicked = this.viewSelectorItemClicked.bind(this);
    }

    viewSelectorItemClicked (item) {
        if(typeof this.props.viewSelectorItemClicked === 'function'){
            this.props.viewSelectorItemClicked(item);
        }
    }

    render () {
        let _self = this;
        let str2Dclass = "view-selector-item";
        let str3Dclass = "view-selector-item";

        if (this.props.currentView === '2d') {
            str2Dclass += " active";
        } else {
            str3Dclass += " active";
        }
        return (
            <div className="view-selector">
                <span
                    className={str2Dclass}
                    onClick={function () { _self.viewSelectorItemClicked('2d') }}
                >2D</span>
                <span
                    className={str3Dclass}
                    onClick={function () { _self.viewSelectorItemClicked('3d') }}
                >3D</span>
            </div>
        )
    }
}