import React from 'react';

import { Engine, Scene } from 'react-babylonjs';

import './styles.css';

import SceneManager from './sceneManager';
import { Container, BeginEvent, Keyboard } from './simulator';

class SimulatorEngine extends React.Component {
    constructor (props) {
        super(props);

        this.state = {
            showPath: false,
        }

        this.board = new SceneManager(
            this.props.size,
            this.props.modelStorePath,
            this.props.currentView,
            {
                posCueBallWhite: {
                    x: this.props.posCueBallWhite.x,
                    z: this.props.posCueBallWhite.z
                },
                posCueBallYellow: {
                    x: this.props.posCueBallYellow.x,
                    z: this.props.posCueBallYellow.z
                },
                posTargetBall: {
                    x: this.props.posTargetBall.x,
                    z: this.props.posTargetBall.z
                }
            },
            {
                cueLine: this.props.cueLine,
                cueAngle: this.props.cueAngle,
                sideEnglish: this.props.sideEnglish,
                topSpin: this.props.topSpin,
                speed: this.props.speed,
            }
        );

        this.simulatorDom = new React.createRef();
    }

    meshPicked = ( mesh ) => {
        // console.log('mesh picked', mesh);
    }

    drawBallPath = (value) => {
        this.board.drawBallPath(value);
    }

    onSceneMount = ( e ) => {
        const { canvas, scene } = e;
        this.board.initializeScene(scene, canvas);
    }

    changeView (option) {
        this.board.changeView(option);
    }

    toggleGrids = (visible) => {
        this.board.toggleGrids(visible);
    }

    playSimulation = () => {
        this.board.playSimulation();
    }

    updateControlOptions = (data) => {
        this.board.updateControlOptions(data);
    }

    componentDidMount = () => {
        // console.log(this.simulatorDom.current);
        // var controller1 = new Container(
        //     this.simulatorDom.current,
        //     text => {})

        // controller1.eventQueue.push(new BeginEvent())

        // var keyboard = new Keyboard()

        // var lastTime = performance.now()

        // function sampleInputs() {
        //     var t = performance.now() - lastTime
        //     lastTime = performance.now()
        //     var inputs = keyboard.getEvents(t)
        //     inputs.forEach(i => controller1.inputQueue.push(i))
        //     requestAnimationFrame(t => {
        //         sampleInputs()
        //     })
        // }
        // sampleInputs()
        // controller1.animate(performance.now())
    }
    render () {
        return (
            // <div ref={this.simulatorDom} style={{height:'600px'}}></div>
            <Engine canvasId="carom-simulator-canvas">
                <Scene
                    onMeshPicked={this.meshPicked}
                    onSceneMount={this.onSceneMount}
                />
            </Engine>
        )
    }
}

export default SimulatorEngine;