import * as BABYLON from 'babylonjs';
import {
    TABLE_LENGTH,
    BALL_SIZE,

    fileTableModel,
    fileBatModel,

} from '../constants';

import CBillard from './OriginSimulatorEngine/cBillard';

export default class SceneManager {
    constructor (
        size,
        modelStorePath,
        currentView,
        ballPositions,
        controlOptions
    ) {
        this.scene = undefined;
        this.canvas = undefined;

        this.size = size;

        this.ballRadius = 400 * BALL_SIZE / (this.size * 100);

        this.modelStorePath = modelStorePath;

        this.initBallPosition = ballPositions;
        this.ballPositions = {
            posCueBallWhite: {
                x: TABLE_LENGTH * ballPositions.posCueBallWhite.x / size,
                z: TABLE_LENGTH * ballPositions.posCueBallWhite.z / size
            },
            posCueBallYellow: {
                x: TABLE_LENGTH * ballPositions.posCueBallYellow.x / size,
                z: TABLE_LENGTH * ballPositions.posCueBallYellow.z / size
            },
            posTargetBall: {
                x: TABLE_LENGTH * ballPositions.posTargetBall.x / size,
                z: TABLE_LENGTH * ballPositions.posTargetBall.z / size
            }
        };

        this.currentView = currentView;

        this.cameras = {
            arcCam: undefined,
            topCam: undefined
        };

        this.objects = {
            table: undefined,
            bat: undefined,
            cueBallWhite: undefined,
            cueBallYellow: undefined,
            targetBall: undefined,
            batContactLine: undefined,
            batLookatPoint: undefined,
            directionLine: undefined,
        };

        this.controlOptions = controlOptions;

        this.grids = [];

        this.pathCueWhite = undefined;
        this.pathCueYellow = undefined;
        this.pathTargetBall = undefined;

        this.billard = new CBillard(
            310,
            undefined,
            300,
        );
        
        this.timerCount = 0;

        this.RAp0 = [0, 0, 0]
        this.RAq0 = [0, 0, 0]
        this.RAr0 = [0, 0, 0]
    }

    drawBallPath = (value) => {
        let res = this.pathData

        if(!res || res.length == 0) return;
        if(this.pathCueWhite){
            this.pathCueWhite.dispose()
        } 
        
        if(this.pathCueYellow){
            this.pathCueYellow.dispose()
        }

        if(this.pathTargetBall){
            this.pathTargetBall.dispose()
        } 
        
        if (value) {
            var whiteBallPoints = [];
            for (var i = 0; i < 10000; i++) {
                if (res[i]) {
                    whiteBallPoints.push(new BABYLON.Vector3(
                        TABLE_LENGTH * res[i].X / this.size,
                        this.ballRadius / 2,
                        TABLE_LENGTH * res[i].Y / this.size
                    ))
                }
            }
            this.pathCueWhite = BABYLON.MeshBuilder.CreateLines(
                'lines',
                {
                    points: whiteBallPoints
                }, 
                this.scene
            );

            var yellowBallPoints = [];
            var colorYellow = [];
            for (i = 10000; i < 20000; i++) {
                if (res[i]) {
                    yellowBallPoints.push(new BABYLON.Vector3(
                        TABLE_LENGTH * res[i].X / this.size,
                        this.ballRadius / 2,
                        TABLE_LENGTH * res[i].Y / this.size
                    ));
                    colorYellow.push(new BABYLON.Color4(0.8, 0.8, 0, 1));
                }
            }

            this.pathCueYellow = BABYLON.MeshBuilder.CreateLines(
                'lines',
                {
                    points: yellowBallPoints,
                    colors: colorYellow
                }, 
                this.scene
            );

            var redBallPoints = [];
            var colorRed = [];

            for (i = 20000; i < 30000; i++) {
                if (res[i]) {
                    redBallPoints.push(new BABYLON.Vector3(
                        TABLE_LENGTH * res[i].X / this.size,
                        this.ballRadius / 2,
                        TABLE_LENGTH * res[i].Y / this.size
                    ));
                    colorRed.push(new BABYLON.Color4(0.8, 0, 0, 1));
                }
            }

            this.pathTargetBall = BABYLON.MeshBuilder.CreateLines(
                'lines',
                {
                    points: redBallPoints,
                    colors: colorRed
                }, 
                this.scene
            );
        }        
    }

    configCameras = () => {
        const _self = this;
        _self.cameras.arcCam = new BABYLON.ArcRotateCamera("Camera-Arc", 4.44, 1.05, 300, BABYLON.Vector3.Zero(), _self.scene);
        _self.cameras.arcCam.attachControl(_self.canvas);
        _self.cameras.arcCam.upperBetaLimit = 1.5;
        _self.cameras.arcCam.wheelPrecision = 0.2;
        
        _self.cameras.arcCam.upperRadiusLimit = 500;
        _self.cameras.arcCam.lowerRadiusLimit = 100;
        
        _self.cameras.topCam = new BABYLON.ArcRotateCamera("Camera-Top", -Math.PI/2, 0, 330, BABYLON.Vector3.Zero(), _self.scene);

        _self.cameras.topCam.upperBetaLimit = 0;
        _self.cameras.topCam.lowerBetaLimit = 0;

        _self.cameras.topCam.upperAlphaLimit = -Math.PI/2;
        _self.cameras.topCam.lowerAlphaLimit = -Math.PI/2;

        _self.cameras.topCam.upperRadiusLimit = 330;
        _self.cameras.topCam.lowerRadiusLimit = 330;

        _self.cameras.topCam.mode = BABYLON.Camera.ORTHOGRAPHIC_CAMERA;
        _self.cameras.topCam.orthoTop = 130;
        _self.cameras.topCam.orthoBottom = -130;
        _self.cameras.topCam.orthoLeft = -240;
        _self.cameras.topCam.orthoRight = 240;

        _self.changeView(_self.currentView);
    }

    loadModels = () => {
        const _self = this;
        const { modelStorePath } = this;

        BABYLON.SceneLoader.ImportMesh("", modelStorePath, fileBatModel, _self.scene, function (newMeshes) {
            _self.objects.bat = newMeshes[0];
            _self.objects.bat.position.y = 50;
            _self.objects.bat.position.z = 90;

            _self.objects.batLookatPoint = new BABYLON.MeshBuilder.CreateSphere(
                'look-at',
                {
                    diameter: 4
                },
                _self.scene
            )
            
            // _self.objects.batLookatPoint.parent = _self.objects.bat;
            _self.objects.batLookatPoint.position.y = 30;
            _self.objects.batLookatPoint.visibility = false;
            
            BABYLON.SceneLoader.ImportMesh("", modelStorePath, fileTableModel, _self.scene, function (newMeshes) {
                _self.objects.table = newMeshes;
                for(var obj of newMeshes){
                    if(obj.name === 'carpet'){
                        // assign bump
                        obj.material.bumpTexture = new BABYLON.Texture(modelStorePath+'carpet-bump.png', _self.scene);
                        // end assign bump
                    }
                }
                
                _self.configCueBalls();
                _self.updateBat(_self.controlOptions);
            });
        });
    }

    updateControlOptions = (data) => {
        this.controlOptions = data;
        this.updateBat(data);
    }

    updateBat = (data) => {
        const {
            cueLine,
            cueAngle,
            sideEnglish,
            topSpin,
        } = data;
        const {size, ballRadius} = this;
        const {bat, batLookatPoint, directionLine, cueBallWhite} = this.objects;

        let deltaSideEnglish = TABLE_LENGTH * sideEnglish / size;
        let deltaTopSpin = TABLE_LENGTH * topSpin / size;

        bat.position.x = cueBallWhite.position.x
                         - deltaSideEnglish * Math.sin(cueLine)
                         - deltaTopSpin * Math.sin(cueAngle) * Math.cos(cueLine);
        bat.position.z = cueBallWhite.position.z
                         + deltaSideEnglish * Math.cos(cueLine)
                         - deltaTopSpin * Math.sin(cueAngle) * Math.sin(cueLine);
        bat.position.y = ballRadius/2
                         + deltaTopSpin * Math.cos(cueAngle);
        
        bat.rotation = new BABYLON.Vector3(
            0,
            -cueLine,
            Math.PI/2 + cueAngle
        );

        // update look at point
        var lookAtPoint = Object.assign({}, bat.position);
        lookAtPoint.y += 40 * Math.sin(cueAngle);
        lookAtPoint.x += 40 * Math.cos(cueAngle) * Math.cos(cueLine);
        lookAtPoint.z += 40 * Math.cos(cueAngle) * Math.sin(cueLine);
        batLookatPoint.position = lookAtPoint;
        batLookatPoint.lookAt(bat.position);
        // end update look at point

        // update direction line
        directionLine.position.x = cueBallWhite.position.x;
        directionLine.position.z = cueBallWhite.position.z;
        directionLine.position.y = ballRadius/2;

        directionLine.rotation.y = Math.PI - cueLine;
        // end update direction line
    }

    generateReflectionMaterial = (root, others) => {
        let {scene} = this;
		
		var probe = new BABYLON.ReflectionProbe("satelliteProbe" + root.name, 512, scene);
		for (var index = 0; index < others.length; index++) {
			probe.renderList.push(others[index]);			
		}
		
        root.material.reflectionTexture = probe.cubeTexture;
        root.material.reflectivityColor = new BABYLON.Color3(0.2, 0.2, 0.2);
		
		root.material.reflectionFresnelParameters = new BABYLON.FresnelParameters();
        root.material.reflectionFresnelParameters.bias = 0;
		probe.attachToMesh(root);
    }
    
    configCueBalls = () => {
        const _self = this;
        const {
            modelStorePath,
            ballRadius
        } = this;

        const { table } = this.objects;
        const {arcCam} = this.cameras;
        let carpet, rail;
        for(var obj of table) {
            if (obj.name === 'carpet') {
                carpet = obj;
            } else if (obj.name === 'carpet-side') {
                rail = obj;
            }
        }
        let {
            posCueBallWhite,
            posCueBallYellow,
            posTargetBall
        } = this.ballPositions;

        _self.objects.cueBallWhite = BABYLON.Mesh.CreateSphere("cue-ball-white", 16, ballRadius, _self.scene);
        _self.objects.cueBallWhite.position.y = ballRadius/2;
        _self.objects.cueBallWhite.position.x = posCueBallWhite.x;
        _self.objects.cueBallWhite.position.z = posCueBallWhite.z;
        // arcCam.setTarget(_self.objects.cueBallWhite);

        var matCueBallWhite = new BABYLON.StandardMaterial("redMat", _self.scene);
        matCueBallWhite.diffuseTexture = new BABYLON.Texture(modelStorePath + "cueball-white.jpg", _self.scene);
        _self.objects.cueBallWhite.material = matCueBallWhite;

        _self.objects.cueBallYellow = BABYLON.Mesh.CreateSphere("cue-ball-yellow", 16, ballRadius, _self.scene);
        _self.objects.cueBallYellow.position.y = ballRadius/2;
        _self.objects.cueBallYellow.position.x = posCueBallYellow.x;
        _self.objects.cueBallYellow.position.z = posCueBallYellow.z;

        var matCueBallYellow = new BABYLON.StandardMaterial("redMat", _self.scene);
        matCueBallYellow.diffuseTexture = new BABYLON.Texture(modelStorePath + "cueball-yellow.jpg", _self.scene);
        _self.objects.cueBallYellow.material = matCueBallYellow;

        _self.objects.targetBall = BABYLON.Mesh.CreateSphere("cue-ball-red", 16, ballRadius, _self.scene);
        _self.objects.targetBall.position.y = ballRadius/2;
        _self.objects.targetBall.position.x = posTargetBall.x;
        _self.objects.targetBall.position.z = posTargetBall.z;

        var matTargetBall = new BABYLON.StandardMaterial("redMat", _self.scene);
        matTargetBall.diffuseTexture = new BABYLON.Texture(modelStorePath + "targetball.jpg", _self.scene);
        _self.objects.targetBall.material = matTargetBall;

        _self.generateReflectionMaterial(_self.objects.cueBallWhite, [_self.objects.cueBallYellow, _self.objects.targetBall, _self.objects.bat, carpet, rail]);
        _self.generateReflectionMaterial(_self.objects.cueBallYellow, [_self.objects.cueBallWhite, _self.objects.targetBall, _self.objects.bat, carpet, rail]);
        _self.generateReflectionMaterial(_self.objects.targetBall, [_self.objects.cueBallWhite, _self.objects.cueBallYellow, _self.objects.bat, carpet, rail]);

        //contact points
        _self.objects.ray = new BABYLON.Ray();
        _self.objects.rayHelper = new BABYLON.RayHelper(_self.objects.ray);
        var localMeshDirection = new BABYLON.Vector3(0, 0, 1);
        // _self.cameras.arcCam.setTarget(_self.objects.batLookatPoint)
        
        _self.objects.rayHelper.attachToMesh(_self.objects.batLookatPoint, localMeshDirection);
        // _self.objects.rayHelper.show(_self.scene);
    }

    configGrids = () => {
        const _self = this;
        const grayRate = 0.7;
        const color = [new BABYLON.Color4(grayRate,grayRate,grayRate,1), new BABYLON.Color4(grayRate,grayRate,grayRate,1)];
        for (var i = 0; i < 15; i++) {
            let gridPoints = [
                new BABYLON.Vector3(-TABLE_LENGTH*7/16 + i * TABLE_LENGTH/16, 2, -TABLE_LENGTH/4),
                new BABYLON.Vector3(-TABLE_LENGTH*7/16 + i * TABLE_LENGTH/16, 2, TABLE_LENGTH/4)
            ];
            _self.grids.push(BABYLON.MeshBuilder.CreateLines("lines", {points: gridPoints, colors: color}, _self.scene));
        }
        for (i = 0; i < 7; i++) {
            let gridPoints = [
                new BABYLON.Vector3(-TABLE_LENGTH/2, 2, -TABLE_LENGTH*3/16 + i * TABLE_LENGTH/16),
                new BABYLON.Vector3(TABLE_LENGTH/2, 2, -TABLE_LENGTH*3/16 + i * TABLE_LENGTH/16)
            ];
            _self.grids.push(BABYLON.MeshBuilder.CreateLines("lines", {points: gridPoints, colors: color}, _self.scene));
        }
    }

    toggleGrids = (visible) => {
        const {grids} = this;
        for (var i = 0; i < grids.length; i++) {
            grids[i].visibility = visible;
        }
    }

    initializeScene = (scene, canvas) => {
        let _self = this;
        _self.scene = scene;
        _self.canvas = canvas;

        _self.scene.clearColor = new BABYLON.Color3(0.05, 0.05, 0.05);
        _self.objects.directionLine = new BABYLON.MeshBuilder.CreateLines(
            'direction-line',
            {
                points: [
                    new BABYLON.Vector3(0, 0, 0),
                    new BABYLON.Vector3(600, 0, 0)
                ],
                colors: [
                    new BABYLON.Color4(0, 0, 0.8, 1),
                    new BABYLON.Color4(0, 0, 0.8, 1),
                ]
            }
        )

        new BABYLON.HemisphericLight('light', BABYLON.Vector3.Up(), scene);

        _self.configCameras();
        _self.loadModels();
        _self.configGrids();

        _self.scene.getEngine().runRenderLoop(() => {
            if (scene) {
                scene.render();
                _self.animateBalls();
            }
        });

        canvas.addEventListener("pointerdown", _self.onPointerDown, false);
        canvas.addEventListener("pointerup", _self.onPointerUp, false);
        canvas.addEventListener("pointermove", _self.onPointerMove, false);
    }

    getGroundPosition = () => {
        let {scene} = this;
        var pickinfo = scene.pick(scene.pointerX, scene.pointerY, function (mesh) { return mesh.name == 'carpet'; });
        if (pickinfo.hit) {
            return pickinfo.pickedPoint;
        }

        return null;
    }

    onPointerDown = (evt) => {
        let _self = this;
        let {scene, canvas} = this;
        if (evt.button !== 0) {
            return;
        }

        // check if we are under a mesh
        var pickInfo = scene.pick(scene.pointerX, scene.pointerY, function (mesh) {return (mesh.name === 'cue-ball-white' || mesh.name === 'cue-ball-yellow' || mesh.name === 'cue-ball-red')});
        if (pickInfo.hit) {
            _self.currentMesh = pickInfo.pickedMesh;
            _self.startingPoint = _self.getGroundPosition(evt);

            if (_self.startingPoint) {
                setTimeout(function () {
                    _self.cameras.arcCam.detachControl(canvas);
                }, 0);
            }
        }
    }

    onPointerUp = () => {
        let _self = this;
        let {startingPoint, canvas} = this;
        if (startingPoint) {
            _self.cameras.arcCam.attachControl(canvas, true);
            _self.startingPoint = null;
            _self.currentMesh = null;
            return;
        }
    }

    onPointerMove = (evt) => {
        let _self = this;
        let {startingPoint, currentMesh} = this;
        if (!startingPoint) {
            return;
        }

        var current = _self.getGroundPosition(evt);

        if (!current) {
            return;
        }
        
        currentMesh.position = current;
        currentMesh.position.y = _self.ballRadius/2

        startingPoint = current;
        _self.updateBat(_self.controlOptions);
    }

    changeView (view) {
        const _self = this;
        
        if (view === '2d') {
            _self.scene.activeCamera = _self.cameras.topCam;
            _self.cameras.arcCam.detachControl(_self.canvas);
            _self.cameras.topCam.attachControl(_self.canvas);
        } else {
            _self.scene.activeCamera = _self.cameras.arcCam;
            _self.cameras.arcCam.attachControl(_self.canvas);
            _self.cameras.topCam.detachControl(_self.canvas);
        }
    }

    moveAndRotateBall = (obj, t, tmp, ) => {
        let range = 10000;
        let index = -1;
        let _self = this;
        let i;
        for(i = tmp * range ; i < (tmp + 1) * range; i++){
            if(_self.pathData[i])
            {
                if(_self.pathData[i].time < (t - _self.t0)){
                    index = i;
                }
            } else { break;}
        }
        if(index >= 0){
            if (index == i-1) {
                return false;
            }
            let data = _self.pathData[index];
            obj.position.x = TABLE_LENGTH * data.X / _self.size;
            obj.position.z = TABLE_LENGTH * data.Y / _self.size;

            let RAp = _self.pathData[index].RAp;
            let RAq = _self.pathData[index].RAq;
            let RAr = _self.pathData[index].RAr;

            const R = 0.03;
            let Rx = (RAp - _self.RAp0[tmp]) / R
            let Ry = (RAq - _self.RAq0[tmp]) / R
            let Rz = (RAr - _self.RAr0[tmp]) / R

            _self.RAp0[tmp] = RAp
            _self.RAq0[tmp] = RAq
            _self.RAr0[tmp] = RAr

            obj.rotate(BABYLON.Axis.X, -Rx, BABYLON.Space.WORLD);
            obj.rotate(BABYLON.Axis.Y, -Rz, BABYLON.Space.WORLD);
            obj.rotate(BABYLON.Axis.Z, -Ry, BABYLON.Space.WORLD);
            return true;
        }
    }
    
    animateBalls = () => {
        let _self = this;
        let { cueBallWhite, cueBallYellow, targetBall } = this.objects;
        if(!_self.pathData || _self.pathData.length == 0) return;
        if (_self.t0 == undefined) return;
        let t = new Date().getTime() / 1000;

        var res1 = this.moveAndRotateBall(cueBallWhite, t, 0);
        var res2 = this.moveAndRotateBall(cueBallYellow, t, 1);
        var res3 = this.moveAndRotateBall(targetBall, t, 2);
        console.log(res1, res2, res3);
        if (res1 == false && res2 == false && res3 == false) _self.pathData = [];
    }

    playSimulation = () => {
        var _self = this;
        const ballPositions = this.initBallPosition;
        const {controlOptions} = this;
        const {
            cueBallWhite,
            cueBallYellow,
            targetBall
        } = this.objects;
        var res = this.billard.Simuler(
            1,
            {
                Xinit: [
                    _self.size * cueBallWhite.position.x / TABLE_LENGTH,
                    _self.size * cueBallYellow.position.x / TABLE_LENGTH,
                    _self.size * targetBall.position.x / TABLE_LENGTH
                ],
                Yinit: [
                    _self.size * cueBallWhite.position.z / TABLE_LENGTH,
                    _self.size * cueBallYellow.position.z / TABLE_LENGTH,
                    _self.size * targetBall.position.z / TABLE_LENGTH
                ],
                RApInit: [0, 0, 0],
                RAqInit: [0, 0, 0],
                RArInit: [0, 0, 0],
                el: controlOptions.sideEnglish,
                er: controlOptions.topSpin,
                alphaQ: controlOptions.cueAngle,
                betaQ: controlOptions.cueLine,
                Vqueue:controlOptions.speed,
            }
        );
        console.log(res)

        if(!res || res.length == 0) return;
        
        this.pathData = res;

        this.t0 = new Date().getTime() / 1000;
    }
}