import React from 'react';

import ViewSelector from './viewSelector';
import SimulatorEngine from './simulatorEngine';
import ControllerCollection from './controller';

import './styles.css';

import {
    INDEX_SIZE,
    INDEX_WHITE_X,
    INDEX_WHITE_Y,
    INDEX_YELLOW_X,
    INDEX_YELLOW_Y,
    INDEX_TARGET_X,
    INDEX_TARGET_Y,
    INDEX_CUELINE,
    INDEX_CUEANGLE,
    INDEX_SIDEENGLISH,
    INDEX_TOPSPIN,
    INDEX_SPEED
} from './constants';

class CaromSimulator extends React.Component {
    constructor () {
        super();

        this.state = {
            currentView : '2d',
            showGrid : true,
            showPath: false,
            size: 310,
            cueLine: 30,
            cueAngle: 5,
            sideEnglish: -8.7,
            topSpin: 7.5,
            speed: 380,
            posCueBallWhite: {},
            posCueBallYellow: {},
            posTargetBall: {}
        }
        this.simulator = React.createRef();
    }
    componentWillMount = () => {
        this.updateSimulateInfo();
    }
    updateSimulateInfo = () => {
        var info = [
            'Santiago  Soto Sanchez',
            '...',
            '0',
            '310',
            '2',
            '1',
            '1.1661666666666661',
            '0.6288333333333332',
            '0.8181666666666672',
            '0.5041666666666665',
            '-0.6036666666666667',
            '-0.003833333333333161',
            '4.015999999999998',
            '0.520497057626843',
            '0.21066799020683835',
            '-0.0013500000000002585',
            '-0.0146999999999997',
        ];
        let size = 2.8;

        let cueLine = parseFloat(info[INDEX_CUELINE]);

        let cueAngle = parseFloat(info[INDEX_CUEANGLE]);

        let sideEnglish = parseFloat(info[INDEX_SIDEENGLISH]);

        let topSpin = parseFloat(info[INDEX_TOPSPIN]);

        let speed = parseFloat(info[INDEX_SPEED]);

        let posCueBallWhite = {
            x: parseFloat(info[INDEX_WHITE_X]),
            z: parseFloat(info[INDEX_WHITE_Y])
        }

        let posCueBallYellow = {
            x: parseFloat(info[INDEX_YELLOW_X]),
            z: parseFloat(info[INDEX_YELLOW_Y])
        }

        let posTargetBall = {
            x: parseFloat(info[INDEX_TARGET_X]),
            z: parseFloat(info[INDEX_TARGET_Y])
        }

        let tmp = {
            size: size,
            cueLine: cueLine,
            cueAngle: cueAngle,
            sideEnglish: sideEnglish,
            topSpin: topSpin,
            speed: speed,
            posCueBallWhite: posCueBallWhite,
            posCueBallYellow: posCueBallYellow,
            posTargetBall: posTargetBall
        }

        this.setState(tmp)
    }

    viewSelectorItemClicked = (item) => {
        this.setState({currentView: item});
        this.simulator.current.changeView(item);
    }

    onToggleShowGrid = (value) => {
        this.setState({showGrid: value})
        this.simulator.current.toggleGrids(value);
    }

    onToggleShowPath = (value) => {
        this.setState({showPath: value})
        this.simulator.current.drawBallPath(value);
    }

    onPlayPressed = () => {
        this.simulator.current.playSimulation();
    }

    onControlOptionsUpdated = (data) => {
        this.simulator.current.updateControlOptions(data);
    }

    componentDidMount = () => {
    }

    render () {
        const {
            currentView,

            showGrid,
            showPath,

            size,
            cueLine,
            cueAngle,
            sideEnglish,
            topSpin,
            speed,

            posCueBallWhite,
            posCueBallYellow,
            posTargetBall,
        } = this.state;
        return (
            <div className="simulator-container">
                <ViewSelector
                    currentView={currentView}
                    viewSelectorItemClicked={this.viewSelectorItemClicked}
                />
                <SimulatorEngine
                    modelStorePath={this.props.modelStorePath}
                    ref={this.simulator}

                    size={size}
                    currentView={currentView}
                    cueLine = {cueLine}
                    cueAngle = {cueAngle}
                    sideEnglish = {sideEnglish}
                    topSpin = {topSpin}
                    speed = {speed}

                    posCueBallWhite = {posCueBallWhite}
                    posCueBallYellow = {posCueBallYellow}
                    posTargetBall = {posTargetBall}
                />
                <ControllerCollection
                    showGrid={showGrid}
                    showPath={showPath}
                    cueLine = {cueLine}
                    cueAngle = {cueAngle}
                    sideEnglish = {sideEnglish}
                    topSpin = {topSpin}
                    speed = {speed}

                    onControlOptionsUpdated={this.onControlOptionsUpdated}

                    onToggleShowGrid={this.onToggleShowGrid}
                    onToggleShowPath = {this.onToggleShowPath}
                    onPlayPressed = {this.onPlayPressed}
                />
            </div>
        )
    }
}

export default CaromSimulator;